<template>
  <div v-theme:column="''" id="show-blogs">
    <h1>博客总览</h1>
    <input
      type="text"
      id="search"
      v-model="search"
      placeholder="请输入文章标题"
      name="search"
      autocomplete="off"
    />
    <!-- 展示每一条博客 -->
    <div class="single-blog" v-for="blog in filteredBlogs">
      <router-link v-bind:to="'/blog/' +blog.id">
        <h2 v-rainbow>{{blog.title}}</h2>
      </router-link>
      <article>{{blog.content}}</article>
    </div>
  </div>
</template>

<script>
export default {
  name: "show-blogs",
  data() {
    return {
      blogs: [],
      search: "",
    };
  },
  // 请求数据
  created() {
    this.$http
      .get("https://blog-837d9-default-rtdb.firebaseio.com/posts.json")
      .then((data) => {
        // 解析json对象
        return data.json();
      })
      .then((data) => {
        var blogsArray = [];
        for (let key in data) {
          console.log(key);
          console.log(data[key]);
          data[key].id = key;
          blogsArray.push(data[key]);
        }
        this.blogs = blogsArray;
      });
  },
  computed: {
    filteredBlogs() {
      return this.blogs.filter((blog) => {
        return blog.title.match(this.search);
      });
    },
  },
};
</script>


<style scoped>
#show-blogs {
  max-width: 800px;
  margin: 0 auto;
}

.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #eee;
  border: 1px dotted #aaa;
}

#show-blogs a {
  color: #444;
  text-decoration: none;
}

input[type="text"] {
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
  border: 0;
  outline: none;
}
/* input[type="text"]:focus {
  border: 1px solid #6677cc;
} */
</style>


