<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form v-if="!submited">
      <label for>博客标题:</label>
      <input type="text" v-model="blog.title" required />

      <label for>博客内容:</label>
      <textarea v-model="blog.content" required></textarea>

      <div id="checkboxes">
        <label for>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories" />
        <label for>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories" />
        <label for>React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories" />
        <label for>Angular4.js</label>
        <input type="checkbox" value="Angular4.js" v-model="blog.categories" />
      </div>

      <label for>作者:</label>
      <select v-model="blog.author">
        <!-- <option checked></option> -->
        <option v-for="author in authors">{{author}}</option>
      </select>
      <button @click.prevent="post">添加博客</button>
    </form>
    <div>
      <h3 v-if="submited">您的博客已经发布成功</h3>
    </div>
    <hr v-if="submited" />
    <!-- 展示部分 -->
    <div class="preview" v-if="submited">
      <h3>博客总览</h3>
      <p>博客标题:{{blog.title}}</p>
      <p>博客内容:</p>
      <p>{{blog.content}}</p>
      <p>博客分类:</p>
      <ul>
        <li v-for="(categorie,index) in blog.categories" :key="index">{{categorie}}</li>
      </ul>
      <p>作者:{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "add-blog",
  data() {
    return {
      blog: {
        title: "",
        content: "",
        categories: [],
        author: "",
      },
      authors: ["Hemiah", "Henry", "Bucky", "Taylor"],
      //   判断是否提交
      submited: false,
    };
  },
  methods: {
    post() {
      this.$http
        .post(
          "https://blog-837d9-default-rtdb.firebaseio.com/posts.json",
          this.blog
        )
        .then((data) => {
          console.log(data);
          this.submited = true;
        });
    },
  },
};
</script>


<style scoped>
#add-blog * {
  box-sizing: border-box;
}

#add-blog {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}

label {
  display: block;
  margin: 20px 0 10px;
}

input[type="text"],
textarea,
select {
  display: block;
  width: 100%;
  padding: 8px;
}

textarea {
  height: 200px;
}

#checkboxes label {
  display: inline-block;
  margin-top: 20px;
}

#checkboxes input {
  display: inline-block;
  margin-right: 10px;
}

button {
  display: block;
  margin: 20px 0;
  background: crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}

#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}

h3 {
  margin-top: 10px;
}
</style>


