<template>
  <div id="single-blog" v-rainbow>
    <h1>{{blog.title}}</h1>
    <article>{{blog.content}}</article>
    <p>作者: {{blog.author}}</p>
    <p>分类:</p>
    <ul>
      <li v-for="category in blog.categories" :key="category">{{category}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "single-blog",
  data() {
    return {
      //   id: this.$route.params.id,
      id: this.$route.params.id,
      blog: {},
    };
  },
  created() {
    this.$http
      .get(
        "https://blog-837d9-default-rtdb.firebaseio.com/posts/" +
          this.id +
          ".json"
      )
      .then((data) => {
        return data.json();
      })
      .then((data) => {
        this.blog = data;
        console.log(data);
      });
  },
};
</script>
<style>
#single-blog {
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
  background: #eee;
  border: 1px dotted #aaa;
}
</style>


